import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import Tag from 'c%/Tag.vue'
export default{
	mixins: [ common,globals ],
	components: {
		Tag,
	},
	data () {
		return {
			pk: 'ht_id',
			map_lesson_type: { 0:'班级作业',1:'一对一作业',2:'一对多作业' },
			data: []
		}
	},
	methods: {
		pushHomework (item) {
			this.$rest('homework_tasks/wechat_notify?ht_id='+item.ht_id).get()
			.success(data=>{
				this.toast('推送成功！','success')
				this.downCallback()
			}).error(response=>{
				this.toast(response.body.message||'推送失败！','warn')
			})	
		},
		getEmployeeAvatar (eid) {
			let employees = this.$store.state.employees,
				result = ''
			if(employees&&employees.length&&eid){
				let obj = employees.find(e=>e.eid==eid)
				if(obj) {
					result = obj.photo_url
				}
			}
			return result
		},
		detail (item) {
			this.$router.push({path:'./list/'+item.ht_id,params:{id:item.ht_id}})
		},
		formatDate (time) {
			return String(time).substring(0,10)
		},
	},
	computed: {
		top () {
			let top = this.headerHeight
			return top
		},
		style () {
			let top = this.top
			return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
		},
		data_uri () {
			let uri = util.sprintf('homework_tasks?eid='+this.eid$)
			return uri
		}
	}
}



